



















import Vue from "vue";

export default Vue.extend({
  name: "ProjectDetailsOverlay",
  props: {
    visible: Boolean,
    color: String,
    title: String,
    htmlContent: String,
    oldYScroll: Number
  },
  methods: {
    getImage: function(url: string) {
      console.log("fetching image " + url);
    },
    closeMethod: function(oldYScroll: number) {
      this.$emit('close');
      window.scrollTo(0, oldYScroll);
    }
  }
});
